/* 
  * reset 
  * The purpose of reset is not to allow default styles to be consistent across all browsers, but to reduce the potential problems of default styles.
  * create by jsliang
*/

/**  - clearance of inner and outer margins **/
body, h1, h2, h3, h4, h5, h6, hr, p, blockquote, /*  - structural elements */
dl, dt, dd, ul, ol, li, /*  - list elements */
pre, /*  - text formatting elements */
form, fieldset, legend, button, input, textarea, /*  - from elements */
th, td /*  - table elements */ {
  margin: 0;
  padding: 0;
}

/**/
body{
  margin:0;padding:0;font-size:"";box-sizing: border-box;
}

body,html{
  text-size-adjust: none;/**/
  width:100%;
  height:100%;
}

*{text-decoration: none;list-style:none;}

img{border:0px;}

/**  - setting the default font **/
body, button, input, select, textarea {
  
}
h1, h2, h3, h4, h5, h6, button, input, select, textarea { font-size: 100%; }

/**  - reset the list element **/
ul, ol { list-style: none; }

/**  - reset the text format element **/
a, a:hover { text-decoration: none; }

/**  - reset the form element **/
button { cursor: pointer; }
input { font-size: 18px; outline: none; }

/**  - reset the table element **/
table { border-collapse: collapse; border-spacing: 0; }

/**  - image responsize **/
img { border: 0; display: inline-block; width: 100%; max-width: 100%; height: auto; vertical-align: middle; }

/* 
    * box-sizingcontent-boxpaddingdivborder-box
    * set border-box for box-sizing when you use div, it solve the problem when you add padding and don't want to make the div width bigger
*/
div, input { box-sizing: border-box; }

/**  - clear float **/
.jsbao-clear:after, .clear:after {
  content: '\20';
  display: block;
  height: 0;
  clear: both;
}
.jsbao-clear, .clear {
  *zoom: 1;
}

/** inputplaceholder - set input placeholder **/
input::-webkit-input-placeholder { color: #919191; font-size: .26rem } /* Webkit browsers */
input::-moz-placeholder { color: #919191; font-size: .26rem } /* Mozilla Firefox */
input::-ms-input-placeholder { color: #919191; font-size: .26rem } /* Internet Explorer */