@import "reset.css";
.flexbox_row_center_center {
  display: flex;
  justify-content: center;
  align-items: center;
}
.flexbox_row_around_center {
  display: flex;
  justify-content: space-around;
  align-items: center;
}
.cursor_pointer {
  cursor: pointer;
}
@keyframes rotate {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
@-moz-keyframes rotate {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
@-webkit-keyframes rotate {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
@-o-keyframes rotate {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
@keyframes bounceInLeft {
  0% {
    left: 0%;
  }
  25% {
    /*  opacity: 1;*/
    left: 25%;
  }
  50% {
    left: 50%;
  }
  75% {
    left: 75%;
  }
  100% {
    left: 100%;
  }
}
