.main_container {
  width: 100%;
  height: 100%;
}
.main_header {
  border-bottom: 1px solid rgba(0, 21, 41, 0.08);
  -webkit-box-shadow: 1px 1px 4px rgba(0, 21, 41, 0.08);
  box-shadow: 1px 1px 4px rgba(0, 21, 41, 0.08);
}
.main_breadcrumb {
  padding: 16px 26px;
  background-color: #fff;
  border-bottom: 1px solid rgba(0, 21, 41, 0.08);
}
.main_content {
  margin-top: 4px;
}
